
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'Footer',
  props: {
    buildNumber: {
      type: String,
      require: true
    },
    creditsLabel: {
      type: String,
      require: true
    },
    creditsModalId: {
      type: String,
      require: true
    },
    zoomEnabled: {
      type: Boolean,
      require: false
    }
  },
  emits: {
    zoomFontSize(payload: { baseFontSize: number }) {
      return payload.baseFontSize > 0;
    }
  },
  data() {
    return {
      baseFontSize: 1
    }
  },
  computed: {
    zoomPercent() : string {
      return Math.floor(this.baseFontSize * 100) + '%'
    }
  },
  methods: {
    zoomIn() {
      if (this.baseFontSize < 3) {
        this.baseFontSize += 0.1
      }
      this.$emit('zoomFontSize', { baseFontSize: this.baseFontSize })
    },
    zoomOut() {
      if (this.baseFontSize > 0.51) {
        this.baseFontSize -= 0.1
      }
      this.$emit('zoomFontSize', { baseFontSize: this.baseFontSize })
    },
    zoomReset() {
      this.baseFontSize = 1
      this.$emit('zoomFontSize', { baseFontSize: this.baseFontSize })
    }
  }
})
